import React from 'react';
import { navigateTo } from 'gatsby-link';
import PropTypes from 'prop-types';

import pic01 from '../images/pic01.jpg';
import pic02 from '../images/pic02.jpg';
import pic03 from '../images/pic03.jpg';

class PostPage extends React.Component {
  componentDidMount() {
    this.props.onOpenArticle('blogPost', false);
  }

  render() {

    let close = <div className="close" onClick={
                  () => {
                    this.props.onCloseArticle();
                    setTimeout((() => navigateTo('/')), 325);
                  }
                }>
                </div>;
    let blogDataArr = this.props.data.allMarkdownRemark.edges;

    return (
      <article id="blog" className={`active ${this.props.articleTimeout ? 'timeout' : ''}`} style={{display:'none'}}>
        <h2 className="major">Blog</h2>
        <span className="image blog_page"><img src={pic03} alt="" /></span>
        {blogDataArr.map(({node}) => (
          <div key={node.id}>
            <header>
              <h3>{node.frontmatter.title}</h3>
              <p>{node.frontmatter.date} - {node.timeToRead} minute read</p>
            </header>
            <div dangerouslySetInnerHTML={{ __html: node.html }} />
          </div>
        ))}
        <span className="link" onClick={
          () => {
            this.props.onCloseArticle();
            setTimeout((() => navigateTo('/')), 325);
          }
        }>Let's go back home...
        </span>
      </article>
    )
  }
}

PostPage.propTypes = {
  route: PropTypes.object,
  article: PropTypes.string,
  articleTimeout: PropTypes.bool,
  onCloseArticle: PropTypes.func,
  timeout: PropTypes.bool
}

export default PostPage;

export const blogQuery = graphql`
  query BlogQuery {
    site {
      siteMetadata {
        title
        description
      }
    }
    allMarkdownRemark(sort: {fields: [frontmatter___date], order: DESC}) {
      totalCount
      edges {
        node {
          id
          frontmatter {
            title
            date(formatString: "DD MMMM, YYYY")
          }
          html
          excerpt
          timeToRead
        }
      }
    }
  }
`;


